.clothes {
    width: 89%;
    margin: 0 auto;
    margin-top: 100px;

    .clothes-wrapper-title {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        .clothes-title {
            font-family: 'Spartan';
            letter-spacing: 2px;
            font-weight: bold;
            font-size: 22px;
        }

        .clothes-filter-menu {
            width: 60%;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            .clothes-filter-btn {
                font-size: 12px;
                opacity: 60%;
                cursor: pointer;
                font-family: 'Spartan';

                input {
                    display: none;
                }
            }
        }
        @media screen and (max-width: 370px) {
            .clothes-filter-menu {
                min-width: 220px;
            }
        }
    }
    .clothes-card{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    @media screen and (max-width: 370px) {
        .clothes-card {
            justify-content: center;
        }
    }
    
    .clothes-btn-see-all {
        width: 100%;
        background: #F8F8F8;

        button {
            width: 100%;
            height: 48px;
            border: none;

            font-family: 'Spartan';
            letter-spacing: 1px;
            font-weight: 500;
            font-size: 13px;
            opacity: 80%;
            margin-top: 1.5%;
            cursor: pointer;
        }
        @media screen and (max-width: 370px) {
            button {
                margin-top: 5%;
            }
        }  
    }
    @media screen and (max-width: 370px) {
        .clothes-wrapper-title {
            justify-content: center;
        }
    }
}